#component {
	.sidebar {
		width: 230px;
		.el-menu-item {
			height: 30px;
			line-height: 30px;
		}
		.el-menu-item-group__title {
			padding-top: 10px;
		}
		.is-active {
			color: #48576a;
		}
		.active {
			color: #1989fa;
		}
	}
	.right {
		width: calc(100% - 200px);
		flex: 1;
		padding: 20px;
		overflow: hidden;
	}
}